<template>
  <div class="left-content" :key="instanceLKey">
    <Inspection class="section-box top-section" />
    <HiddenDefect class="section-box middel-section" />
    <WorkOrder class="section-box bottom-section" />
  </div>
  <div class="middle-content" :key="instanceTKey">
    <MiddleIndicator
      icon="rate"
      label="计划完成(月)"
      @click="toBiReport('rate')"
      :value="indexData.rate"
      unit="%"
      vColor="#67C8FF"
    />
    <MiddleIndicator
      icon="loss"
      label="损失电量(月)"
      @click="toBiReport('loss')"
      :value="indexData.loss"
      unit="万kWh"
      vColor="#F4D038"
    />
    <MiddleIndicator
      icon="person-total"
      label="在岗人数"
      @click="toBiReport('personTotal')"
      :value="indexData.personTotal"
      unit="人"
      vColor="#00BA24"
    />
  </div>
  <div class="right-content" :key="instanceRKey">
    <Loss class="section-box top-section" />
    <TwoTicket class="section-box middel-section" />
    <Security class="section-box bottom-section" />
  </div>
</template>

<script lang="ts" setup>
  import { ref, reactive, UnwrapRef, onMounted } from 'vue';
  import Inspection from './Inspection.vue';
  import HiddenDefect from './HiddenDefect.vue';
  import WorkOrder from './WorkOrder.vue';
  import MiddleIndicator from '/@/components/Framework/OperationScreen/MiddleIndicator.vue';
  import Loss from './Loss.vue';
  import TwoTicket from './TwoTicket.vue';
  import Security from './Security.vue';
  import { assign } from 'lodash-es';
  import { getAllIndexData } from './data';
  import { addTabPage } from '@/utils/route';
  import { buildUUID } from '@/utils/uuid';

  const indexData: UnwrapRef<any> = reactive({});
  const instanceLKey = ref(buildUUID());
  const instanceTKey = ref(buildUUID());
  const instanceRKey = ref(buildUUID());

  // 获取所有指标数据
  const queryAllIndexData = async () => {
    let data = await getAllIndexData();
    if (data.result) {
      assign(indexData, data.result);
    }
  };

  // 穿透方法
  const toBiReport = async (type: string) => {
    // 参数处理
    let param = {};
    let url = '';
    if (type === 'rate') {
      url = '/bi/elec/planned';
    } else if (type === 'loss') {
      url = '/bi/elec/lossDetail';
    }
    addTabPage(url, '', param);
  };

  // 更新组件函数
  const handleUpdate = () => {
    instanceLKey.value = buildUUID();
    instanceTKey.value = buildUUID();
    instanceRKey.value = buildUUID();
  };

  defineExpose({
    handleUpdate,
  });

  onMounted(() => {
    queryAllIndexData();
  });
</script>

