  .left-content {
    display: flex;
    position: absolute;
    top: 0.9rem;
    left: 0;
    flex-direction: column;
    width: 25vw;
    height: calc(100vh - 0.9rem);
    padding: 0 0.57vw 1.68vh 0.68vw;
  }

  .middle-content {
    display: flex;
    position: absolute;
    top: 0.96rem;
    left: 25vw;
    justify-content: space-between;
    width: 50vw;
  }

  .right-content {
    display: flex;
    position: absolute;
    top: 0.9rem;
    right: 0;
    flex-direction: column;
    width: 25vw;
    height: calc(100vh - 0.9rem);
    padding: 0 0.68vw 1.68vh 0.57vw;
  }

  .section-box {
    flex: 1;
    width: 100%;
    height: 33.3333%;
    overflow: hidden;

    &.top-section {
      padding-bottom: 0.62vh;
